import React, {Component} from 'react';
import quizQuestions from './api/quizQuestions';
import Quiz from './components/Quiz';
import Result from './components/Result';
import './App.css';
import ProgressBar from "./components/ProgressBar";
import Header from "./components/Header";
import StartScreen from "./components/StartScreen";
const initialState = {
    isStarted: !!localStorage.result,
    counter: 0,
    questionId: 1,
    question: '',
    answerOptions: [],
    answer: [],
    answersCount: {},
    result: localStorage.result || ''
}

class App extends Component {
    constructor(props) {
        super(props);

        this.state = initialState;

        this.handleAnswerSelected = this.handleAnswerSelected.bind(this);
    }


    componentDidMount() {
        const shuffledAnswerOptions = quizQuestions.map(question =>
            this.shuffleArray(question.answers)
        );
        this.setState({
            question: quizQuestions[0].question,
            answerOptions: shuffledAnswerOptions[0]
        });
    }

    shuffleArray(array) {
        var currentIndex = array.length,
            temporaryValue,
            randomIndex;

        // While there remain elements to shuffle...
        while (0 !== currentIndex) {
            // Pick a remaining element...
            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex -= 1;

            // And swap it with the current element.
            temporaryValue = array[currentIndex];
            array[currentIndex] = array[randomIndex];
            array[randomIndex] = temporaryValue;
        }

        return array;
    }

    handleAnswerSelected(key) {
        this.setUserAnswer(key.type);

        if (this.state.questionId < quizQuestions.length) {
            setTimeout(() => this.setNextQuestion(), 300);
        } else {
            setTimeout(() => this.setResults(this.getResults()), 300);
        }
    }

    setUserAnswer(answer) {
        const resultObj = {}
        answer.forEach(item =>{
            resultObj[item] =  (this.state.answersCount[item] || 0) + 1;
        })
        this.setState((state, props) => ({
            answersCount: {
                ...state.answersCount,
                ...resultObj
            },
            answer: answer
        }));


    }

    setNextQuestion() {
        const counter = this.state.counter + 1;
        const questionId = this.state.questionId + 1;

        this.setState({
            counter: counter,
            questionId: questionId,
            question: quizQuestions[counter].question,
            answerOptions: quizQuestions[counter].answers,
            answer: []
        });
    }

    getResults() {
        const answersCount = this.state.answersCount;
        const answersCountKeys = Object.keys(answersCount);
        const answersCountValues = answersCountKeys.map(key => answersCount[key]);
        const maxAnswerCount = Math.max.apply(null, answersCountValues);

        return answersCountKeys.filter(key => answersCount[key] === maxAnswerCount);
    }

    setResults(result) {
        if (result.length === 1) {
            this.setState({result: result[0]});
        } else {
            this.setState({result: result[Math.floor(Math.random()*result.length)]});
        }
    }

    renderQuiz() {
        return (
            <Quiz
                answer={this.state.answer}
                answerOptions={this.state.answerOptions}
                questionId={this.state.questionId}
                question={this.state.question}
                questionTotal={quizQuestions.length}
                onAnswerSelected={this.handleAnswerSelected}
            />
        );
    }

    renderResult() {
        return <Result quizResult={this.state.result} tryAgain = {this.tryAgain}/>;
    }



    startGame = () => {
        this.setState({
            isStarted: true
        })
    }

    tryAgain = () => {
        this.setState((state, props) =>({
            ...initialState,
            question: quizQuestions[0].question,
            answerOptions: state.answerOptions,
            result:'',
            isStarted: false
        }))
    }

    render() {
        const {questionId, isStarted } = this.state;
        const progressBarWidth = questionId / quizQuestions.length * 100;
        return (
            <div className="App container">
                <Header/>
                <div className="quizWrapper">
                    {isStarted && !this.state.result ?  <ProgressBar width={progressBarWidth}/>:''}
                    {isStarted
                        ? this.state.result ? this.renderResult() : this.renderQuiz()
                        : <StartScreen startGame = {this.startGame}/>
                    }

                </div>
            </div>
        );
    }
}

export default App;
