import React, {Component} from 'react';
import PropTypes from 'prop-types';
import { CSSTransitionGroup } from 'react-transition-group';
import Question from '../components/Question';
import QuestionCount from '../components/QuestionCount';
import AnswerOption from '../components/AnswerOption';
import QuestionAd from "./QuestionAd";

const initialState = {
  isShowAd: false,
  ad: {}
};

class Quiz extends Component{

  state = initialState;

  chooseAnswer  = (key) => {
    return  (event) => {
      if (key.window && key.window.desc && event.target.closest('.radioCustomButton')) {
        this.setState({
          isShowAd: true,
          ad: key
        })
      } else {
          setTimeout(() =>  this.setState(initialState), 300);
          this.props.onAnswerSelected(key);

      }
    }
  };



  // const chooseAnswer = (key) =>{
  //   if(key.window?.desc){
  //     return(
  //         <QuestionAd desc = {key.window.desc} render = {key.window.render} setNextQuestion =  />
  //     )
  //   }
  // };



   renderAnswerOptions = (key) => {
    return (
        (key.variant === this.state.ad.variant || !this.state.isShowAd) &&
      <AnswerOption
        disabled = {this.state.isShowAd}
        key={key.content}
        answerContent={key.content}
        answerType={key.type}
        answer={this.props.answer}
        answerImage={key.img}
        questionId={this.props.questionId}
        onAnswerSelected={this.chooseAnswer(key)}
      />
    );
  }
  render() {
   const  {isShowAd, ad} = this.state


    return (
        <CSSTransitionGroup
            className="quiz"
            component="div"
            transitionName="fade"
            transitionEnterTimeout={800}
            transitionLeaveTimeout={500}
            transitionAppear
            transitionAppearTimeout={500}
        >
          <div key={this.props.questionId} className={"quiz-padding"}>
            <QuestionCount counter={this.props.questionId} total={this.props.questionTotal}/>
            <Question content={this.props.question}/>
            <ul className="answerOptions">
              {this.props.answerOptions.map(this.renderAnswerOptions)}
            </ul>
              {isShowAd? <QuestionAd ad={ad} setNextQuestion = {this.chooseAnswer(ad)} /> :''}
          </div>
        </CSSTransitionGroup>
    );
  }
}

Quiz.propTypes = {
  answer: PropTypes.arrayOf(PropTypes.string).isRequired,
  answerOptions: PropTypes.array.isRequired,
  question: PropTypes.string.isRequired,
  questionId: PropTypes.number.isRequired,
  questionTotal: PropTypes.number.isRequired,
  onAnswerSelected: PropTypes.func.isRequired
};

export default Quiz;
