    desc: "\xab\u0424\u0435\u043e\u0444\u0430\u043d\u0456\u044f\xbb \u2013 \u0446\u0435 \u043f\u0430\u043c\u2019\u044f\u0442\u043a\u0430 \u0441\u0430\u0434\u043e\u0432\u043e-\u043f\u0430\u0440\u043a\u043e\u0432\u043e\u0433\u043e \u043c\u0438\u0441\u0442\u0435\u0446\u0442\u0432\u0430, \u044f\u043a\u0438\u043c \u043c\u043e\u0436\u043d\u0430 \u043d\u0430\u0441\u043e\u043b\u043e\u0434\u0438\u0442\u0438\u0441\u044f, \u0431\u043b\u0443\u043a\u0430\u044e\u0447\u0438 \u043c\u0456\u0436 \u043b\u0430\u043d\u0434\u0448\u0430\u0444\u0442\u043d\u0438\u0445 \u043a\u043e\u043c\u043f\u043e\u0437\u0438\u0446\u0456\u0439. \n        \u041e\u0434\u043d\u0430 \u0437 \u043d\u0430\u0439\u043f\u043e\u043f\u0443\u043b\u044f\u0440\u043d\u0456\u0448\u0438\u0445 \u043b\u043e\u043a\u0430\u0446\u0456\u0439 \u043f\u0430\u0440\u043a\u0443 \u2013 \u0430\u043b\u044c\u0442\u0430\u043d\u043a\u0430 \u043d\u0430 \u043e\u0441\u0442\u0440\u043e\u0432\u0456 \u043f\u043e\u0441\u0435\u0440\u0435\u0434 \u043e\u0437\u0435\u0440\u0430.\n        \u041d\u0430 \u0442\u0435\u0440\u0438\u0442\u043e\u0440\u0456\u0457 \xab\u0424\u0435\u043e\u0444\u0430\u043d\u0456\u0457\xbb \u0437\u043d\u0430\u0445\u043e\u0434\u0438\u0442\u044c\u0441\u044f \u043a\u0456\u043b\u044c\u043a\u0430 \u0434\u0436\u0435\u0440\u0435\u043b \u0437 \u0447\u0438\u0441\u0442\u043e\u044e \u0432\u043e\u0434\u043e\u044e. \n        <br/><br/>\n        \u041a\u0440\u0456\u043c \u0442\u043e\u0433\u043e, \u0442\u0443\u0442 \u0454 \u0434\u0435\u043a\u0456\u043b\u044c\u043a\u0430 \u043e\u0437\u0435\u0440, \u0432\u0437\u0434\u043e\u0432\u0436 \u044f\u043a\u0438\u0439 \u043c\u043e\u0436\u043d\u0430 \u043d\u0435\u0441\u043f\u0456\u0448\u043d\u043e \u043f\u0440\u043e\u0433\u0443\u043b\u044f\u0442\u0438\u0441\u044f. \u0412\u0435\u043b\u0438\u043a\u0430 \u0442\u0435\u0440\u0438\u0442\u043e\u0440\u0456\u044f \u0439 \u0432\u0456\u0434\u0434\u0430\u043b\u0435\u043d\u0456\u0441\u0442\u044c \u0432\u0456\u0434 \u0430\u0432\u0442\u043e\u043c\u0430\u0433\u0456\u0441\u0442\u0440\u0430\u043b\u0435\u0439 \u0434\u043e\u0437\u0432\u043e\u043b\u044f\u044e\u0442\u044c \u0437\u0430\u0431\u0443\u0442\u0438 \u043f\u0440\u043e \u0448\u0443\u043c \u043c\u0456\u0441\u0442\u0430. \n        \u0421\u044e\u0434\u0438 \u0432\u0430\u0440\u0442\u043e \u043f\u0440\u0438\u0457\u0437\u0434\u0438\u0442\u0438 \u043d\u0430\u0434\u043e\u0432\u0433\u043e \u0456 \u0437 \u043a\u043e\u0448\u0438\u043a\u043e\u043c \u0434\u043b\u044f \u043f\u0456\u043a\u043d\u0456\u043a\u0430.",
    ad: '\u042f\u043a\u0449\u043e \u0432\u0430\u043c \u0434\u043e \u0432\u043f\u043e\u0434\u043e\u0431\u0438 \u043f\u0440\u043e\u0432\u043e\u0434\u0438\u0442\u0438 \u0432\u0438\u0445\u0456\u0434\u043d\u0456 \u0443 \u043f\u0430\u0440\u043a\u0443 \xab\u0424\u0435\u043e\u0444\u0430\u043d\u0456\u044f\xbb, \u0446\u0456\u043a\u0430\u0432\u0438\u043c \u0432\u0430\u0440\u0456\u0430\u043d\u0442\u043e\u043c \u0436\u0438\u0442\u043b\u0430 \u043f\u043e\u0440\u0443\u0447 \u043c\u043e\u0436\u0435 \u0441\u0442\u0430\u0442\u0438 \u0436\u0438\u0442\u043b\u043e\u0432\u0438\u0439 \u043a\u043e\u043c\u043f\u043b\u0435\u043a\u0441 <a class="link" href="https://teremki-bud.com.ua/ru">\xab\u0422\u0435\u0440\u0435\u043c\u043a\u0438\xbb</a>. \n        \u041f\u043e\u0431\u043b\u0438\u0437\u0443 \u0440\u043e\u0437\u0442\u0430\u0448\u0443\u0432\u0430\u043b\u0438\u0441\u044f \u0449\u0435 \u0434\u0432\u0456 \u0432\u0435\u043b\u0438\u043a\u0438\u0445 \u0437\u043e\u043d\u0438 \u0434\u043b\u044f \u0432\u0456\u0434\u043f\u043e\u0447\u0438\u043d\u043a\u0443 \u2013 \u0413\u043e\u043b\u043e\u0441\u0456\u0457\u0432\u0441\u044c\u043a\u0438\u0439 \u043b\u0456\u0441 \u0442\u0430 \u041d\u0430\u0446\u0456\u043e\u043d\u0430\u043b\u044c\u043d\u0438\u0439 \u043c\u0443\u0437\u0435\u0439 \u043d\u0430\u0440\u043e\u0434\u043d\u043e\u0457 \u0430\u0440\u0445\u0456\u0442\u0435\u043a\u0442\u0443\u0440\u0438 \u0442\u0430 \u043f\u043e\u0431\u0443\u0442\u0443 \u0423\u043a\u0440\u0430\u0457\u043d\u0438. \n        \u0410 \u043d\u0430 \u0442\u0435\u0440\u0438\u0442\u043e\u0440\u0456\u0457 \u0441\u0430\u043c\u043e\u0433\u043e \u043a\u043e\u043c\u043f\u043b\u0435\u043a\u0441\u0443 \u0437\u0430\u043f\u043b\u0430\u043d\u043e\u0432\u0430\u043d\u0456 \u043f\u0440\u043e\u0433\u0443\u043b\u044f\u043d\u043a\u043e\u0432\u0456 \u0430\u043b\u0435\u0457, \u0434\u0438\u0442\u044f\u0447\u0456 \u0442\u0430 \u0441\u043f\u043e\u0440\u0442\u0438\u0432\u043d\u0456 \u043c\u0430\u0439\u0434\u0430\u043d\u0447\u0438\u043a\u0438.\n<br/><br/>\n        \u041a\u0440\u0456\u043c \u0442\u043e\u0433\u043e, \u0437\u043e\u0432\u0441\u0456\u043c \u043f\u043e\u0440\u0443\u0447 \u2013 \u0436\u0438\u0442\u043b\u043e\u0432\u0438\u0439 \u043c\u0430\u0441\u0438\u0432 \xab\u0422\u0435\u0440\u0435\u043c\u043a\u0438\xbb \u0437 \u0440\u043e\u0437\u0432\u0438\u043d\u0443\u0442\u043e\u044e \u0456\u043d\u0444\u0440\u0430\u0441\u0442\u0440\u0443\u043a\u0442\u0443\u0440\u043e\u044e \u0442\u0430 \u043c\u0435\u0442\u0440\u043e. \n        \u0422\u0443\u0442 \u0454 \u2013 \u0441\u0430\u043b\u043e\u043d\u0438 \u043a\u0440\u0430\u0441\u0438, \u0430\u043f\u0442\u0435\u043a\u0438, \u0431\u0430\u043d\u043a\u0438, \u043f\u0440\u043e\u0434\u0443\u043a\u0442\u043e\u0432\u0456 \u043c\u0430\u0433\u0430\u0437\u0438\u043d\u0438, \u043c\u0435\u0434\u0438\u0447\u043d\u0438\u0439 \u0442\u0430 \u0441\u0442\u043e\u043c\u0430\u0442\u043e\u043b\u043e\u0433\u0456\u0447\u043d\u0438\u0439 \u043a\u0430\u0431\u0456\u043d\u0435\u0442\u0438, \u0446\u0435\u043d\u0442\u0440\u0438 \u0440\u043e\u0437\u0432\u0438\u0442\u043a\u0443 \u0434\u0438\u0442\u0438\u043d\u0438 \u0442\u0430 \u0448\u043a\u043e\u043b\u0430 \u0456\u043d\u043e\u0437\u0435\u043c\u043d\u0438\u0445 \u043c\u043e\u0432. \n        \u041d\u0430 \u0442\u0435\u0440\u0438\u0442\u043e\u0440\u0456\u0457 \u0416\u041a <a class="link" href="https://teremki-bud.com.ua/ru">\xab\u0422\u0435\u0440\u0435\u043c\u043a\u0438\xbb</a> \u043f\u0440\u0430\u0446\u044e\u0432\u0430\u0442\u0438\u043c\u0435 \u0434\u0438\u0442\u044f\u0447\u0438\u0439 \u0441\u0430\u0434\u043e\u043a. \n',
