import React from 'react';
import PropTypes from 'prop-types';
import {CSSTransitionGroup} from 'react-transition-group';
import quizResults from "../api/quizResults";
import {
    FacebookShareButton,
    TwitterShareButton,
    TelegramShareButton,
    FacebookIcon,
    TwitterIcon,
    TelegramIcon
} from "react-share";

function Result(props) {
    const resultObj = quizResults.find(item => item.id === props.quizResult)
    localStorage.setItem('result', props.quizResult);
    const url = window.location.toString()
          .replace(/(index)\w*\.html/g,'')
          +`index${props.quizResult}.html`;

    return (
        <CSSTransitionGroup
            className="quiz"
            component="div"
            transitionName="fade"
            transitionEnterTimeout={800}
            transitionLeaveTimeout={500}
            transitionAppear
            transitionAppearTimeout={500}
        >
            <div className="result">
                <div className="social-media">
                    <span>Поділіться з друзями!</span>
                    <FacebookShareButton url={url}>
                        <FacebookIcon size={32} round={true}/>
                    </FacebookShareButton>
                    <TwitterShareButton url={url}>
                        <TwitterIcon size={32} round={true}/>
                    </TwitterShareButton>
                    <TelegramShareButton url={url}>
                        <TelegramIcon size={32} round={true}/>
                    </TelegramShareButton>
                </div>
                <h2 className="header">{resultObj.header}</h2>
                <img src={resultObj.img} alt=""/>
                <div className="result__desc" dangerouslySetInnerHTML={{__html: resultObj.desc}}/>
                {resultObj.ad && <div className="result__desc" dangerouslySetInnerHTML={{__html: resultObj.ad}}/>}
                <button className="nextbutton startthegame" onClick={props.tryAgain}>Спробувати ще раз
                    <svg width="24" height="24" xmlns="http://www.w3.org/2000/svg">
                        <path
                            d="M21.883 12l-7.527 6.235.644.765 9-7.521-9-7.479-.645.764 7.529 6.236h-21.884v1h21.883z"></path>
                    </svg>
                </button>
            </div>
        </CSSTransitionGroup>
    );
}

Result.propTypes =
    {
        quizResult: PropTypes.string.isRequired
    }
;

export default Result;
