import React from 'react';
import PropTypes from 'prop-types';

function AnswerOption(props) {
    return (
        <li className={`answerOption ${props.disabled ? 'disabled' : ''}`}>
            <input
                type="radio"
                className={`radioCustomButton`}
                name="radioGroup"
                checked={props.answerType.join(',') === props.answer.join(',')}
                id={props.answerType}
                value={props.answerType}
                disabled={props.answer[0]}
                onChange={props.onAnswerSelected}
            />
            <label htmlFor={props.answerType} className={`radioCustomLabel`}>
                <span>{props.answerContent}</span>
                <img src={props.answerImage} alt=""/>
            </label>
        </li>
    );
}

AnswerOption.propTypes = {
    answerType: PropTypes.arrayOf(PropTypes.string).isRequired,
    answerContent: PropTypes.string.isRequired,
    // answer: PropTypes.arrayOf(PropTypes.toString()).isRequired,
    onAnswerSelected: PropTypes.func.isRequired,
    answerImage: PropTypes.string.isRequired,
};

export default AnswerOption;
