import React from 'react';
import { CSSTransitionGroup } from 'react-transition-group';

const QuestionAd = props => (
    <CSSTransitionGroup
        className="question__ad"
        component="div"
        transitionName="fade"
        transitionEnterTimeout={800}
        transitionLeaveTimeout={500}
        transitionAppear
        transitionAppearTimeout={500}
    >
        <div className="question__addess" dangerouslySetInnerHTML={{ __html:  props.ad.window.desc }} />
        <img src={props.ad.window.render} alt="" className="question__adimg"/>
        <button className="nextbutton startthegame" onClick={props.setNextQuestion}>Наступне питання
            <svg width="24" height="24" xmlns="http://www.w3.org/2000/svg">
                <path
                    d="M21.883 12l-7.527 6.235.644.765 9-7.521-9-7.479-.645.764 7.529 6.236h-21.884v1h21.883z"></path>
            </svg>
        </button>
    </CSSTransitionGroup>
)


export default QuestionAd